.inputWrapper {
  position: relative;
  margin-bottom: 16px;
  width: 100%;
}

.input {
  width: 100%;
  padding: 12px 16px;
  font-size: 14px;
  border: 1px solid #d1d5db; /* neutral-300 */
  border-radius: 6px;
  transition: all 0.2s ease;
  background-color: white;
  color: #111827; /* gray-900 */
}

.input:focus {
  outline: none;
  border-color: #3b82f6; /* blue-500 */
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.2); /* blue-100 with opacity */
}
.superInput {
  border-color: #d1d5db; /* neutral-300 */
}

.superInput:focus {
  border-color: #3b82f6; /* blue-500 */
}
.errorInput {
  border-color: #ef4444; /* red-500 */
  background-color: #fef2f2; /* red-50 */
}

.errorInput:focus {
  border-color: #dc2626; /* red-600 */
  box-shadow: 0 0 0 3px rgba(239, 68, 68, 0.2); /* red-100 with opacity */
}
.error {
  display: block;
  margin-top: 4px;
  font-size: 12px;
  color: #ef4444; /* red-500 */
  line-height: 1.5;
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(-5px); }
  to { opacity: 1; transform: translateY(0); }
}