.sidebar {
	position: fixed;
	top: 0;
	left: -100%;
	width: 300px;
	height: 100vh;
	background-color: white;
	transition: left 0.3s ease;
	z-index: 1000;
	box-shadow: 2px 0 5px rgba(0,0,0,0.1);
}

.sidebar.open {
	left: 0;
}

.background {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	background-color: rgba(0,0,0,0.5);
	z-index: 999;
}

.nav {
	display: flex;
	flex-direction: column;
	padding: 50px 20px;
	gap: 15px;
}

.nav a {
	text-decoration: none;
	color: #333;
	font-size: 18px;
	padding: 10px;
	border-radius: 5px;
	transition: all 0.3s ease;
}

.nav a:hover {
	background-color: #f0f0f0;
	color: #0066cc;
}

.nav a.active {
	background-color: #0066cc;
	color: white;
	font-weight: bold;
}

.close {
	position: absolute;
	top: 15px;
	right: 15px;
	background: none;
	border: none;
	cursor: pointer;
	padding: 5px;
}

.close img {
	width: 20px;
	height: 20px;
}
